body {
  font-family: "Roboto", sans-serif !important;
}

/************************** NAVBAR **************************/
nav {
  z-index: 2;
}
/*********MENU LINKS******/

nav a.nav-link {
  /* font-weight: 500; */
  font-size: 0.8rem;
  letter-spacing: 0.037rem;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: white;
  border-top: 0.1875;
  /* color: var(--primary-white) !important; */
  /* border-top: 0.1875rem solid var(--primary-dark) !important; */
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 1.5rem !important;
  height: 6rem !important;
  /* display: none; */
}
@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar {
  background-color: #94bcc0 !important;
  font-size: 1.5rem;
  font-family: Merriweather, sans-serif;
}
.colorLetter {
  color: gold;
  font-size: 2.5rem;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid white !important;
  border-top: 0.1875rem solid rgb(231, 186, 102) !important;
  /* border-top: 0.1875rem solid var(--primary-white) !important;
  border-top: 0.1875rem solid var(--primary-orange) !important; */
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid rgb(231, 186, 102) !important;
  /* border-top: 0.1875rem solid var(--primary-orange) !important; */
}

/************************** HEADER **************************/

.header-wrapper {
  position: relative;
  background: url(wall-and-laptop-background.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto", sans-serif;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.main-info h1 {
  color: gold;
  text-transform: uppercase;
  /* position: absolute; */
}

/* ******Type Text*** */

.typed-text {
  font-size: 2rem;
  color: white;
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

/****** HEADER MAIN BUTTON ****** */

.btn-main-offer {
  border: 1px solid gold;
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: white;
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: red;
  color: white;
  transition: 0.2s ease-in-out;
}

/****** PARTICLES JS****** */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/****** About Me ****** */

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid gold;
}

.about-heading {
  color: gold;
  text-transform: uppercase;
}

@media (max-width: 469px) {
  .profile-img {
    width: 10rem;
  }
}

/****** Services ****** */

.services {
  text-align: center;
  padding-bottom: 2rem;
  background: #94bcc0 !important;
}

.services h1 {
  color: rgb(58, 58, 56);
  text-transform: uppercase;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: rgb(243, 232, 167);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: rgb(46, 45, 45);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 5px solid gold;
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  transition: 0.3s ease-in-out;
  /* background: white; */
}

.services .box:hover {
  background: gold;
  border-bottom: 5px solid darkgoldenrod;
}

/****** Projects ****** */

.project {
  text-align: center;
  padding-bottom: 2rem;
  background: white !important;
}

.project-image {
  width: 15rem;
  border: 1px solid gold;
  padding: 0 0.5rem;
  /* height: 16rem; */
  border-bottom: 5px solid #94bcc0 !important;
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: rgb(238, 235, 235);
  transition: 0.3s ease-in-out;
}
.project h1 {
  color: gold;
  text-transform: uppercase;
}
